/* this css is for the automatic slider ie carausol  */
.slider{

    height: 250px;
    margin: auto;
    position : relative ;
    width: 90%;
    display : grid;
    align-items : center
    
  
}
.slide-track{
  display : flex;
   width : calc(250px * 18) ;
  animation: scroll 10s linear infinite;
  }
  .slide{
    height:200px;
    width : 250px;
    display: flex;
    align-items : center ;
    padding : 15px;
    perspective: 100px;

    }
img{
  width: 100%;
  transition: transform 1s;
}
   .slide::before,
    .slide::after{
      /* background: linear-gradient(to right, rgba(255,255,255,1) 0% , rgba(255, 255, 255 , 0) 10%); */
      content: '';
      height: 100%;
      position: absolute;
      width: 15%;
      z-index: 2;
    } 
.imagecara:hover{
  transform: translateZ(20px);
}  
@keyframes scroll{
0%{
  transform: translateX(0);
}
100%{
 transform: translateX(calc( -100px * 9));
}
}

